@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
@import url(https://fonts.googleapis.com/css?family=Mitr&display=swap);


/** app-wide style configs **/

a {
  color: inherit;
}

a:hover,
a:focus {
  color: grey !important;
}

button:hover {
  cursor: pointer;
}

button:focus {
  outline: 0;
}

/** Ends app-wide style configs **/

/** Bootstrap overrides **/

/** Ends bootstrap overrides **/

/** Bootstrap overrides **/
.navbar-brand {
  color: grey;
  font-size: 24px;
  font-weight: 400;
  font-family: Mitr;
}

.navbar-brand:hover,
.navbar-brand:focus {
  color: grey !important;
}

/** Ends bootstrap overrides **/

.image-section {
  height: 85px;
  width: 85px;
  padding: 0px;
}

.image-card {
  height: 85px;
  width: 85px;
  border-radius: 100%;
}

.name-title {
  font-family: "Mitr";
  font-size: 30px;
}

.job-title {
  font-family: "Mitr";
  font-size: 22px;
  padding-left: 20px;
  padding-right: 20px;
}

.job-description {
  font-family: "Mitr";
  font-size: 16px;
  padding-left: 50px;
  padding-right: 50px;
}

.homepage-container {
  font-family: "Mitr";
}

.what-ido-text {
  font-size: 16px;
}

.contact-me-text {
  font-size: 16px;
}

.blog-posts-list {
  padding-top: 24px;
}

.coming-soon {
  background-color: #333333;
  color: grey;
  height: 92vh;
}

.main-content {
  height: calc(100% - 62px);
  margin-top: 62px;
}

.app-container {
  height: calc(100%);
}

